import { createStore } from 'redux';
import rootReducer from './reducers/rootReducer';

const addLoggingToDispatch = (store) => {
  const rawDispatch = store.dispatch;
  return (action) => {
    console.group(action.type);
    console.log('%c prev_state:', 'color: gray', store.getState());
    console.log('%c action_type:', 'color: gray', action.type);
    const result = rawDispatch(action);
    console.log('%c next_state:', 'color: gray', store.getState());
    console.groupEnd(action.type);
    return result;
  };
};
const loadState = () => ({ authInfo: { logged: false } });
/* eslint-disable no-underscore-dangle */
export const configureStore = () => {
  const INITIAL_STATE = loadState();
  const store = createStore(
    rootReducer,
    INITIAL_STATE,
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
  );

  // set subscribtions to store changes.
  return store;
};
/* eslint-enable no-underscore-dangle */

export const configuredStore = configureStore();
